import { apiClient } from "./client";
import type { UserCreate, UserLogin, UserResponse, Token } from "@/types/api";

export const authApi = {
  register: async (data: UserCreate): Promise<UserResponse> => {
    const response = await apiClient.axios.post<UserResponse>("/auth/register", data);
    return response.data;
  },

  login: async (data: UserLogin): Promise<Token> => {
    const response = await apiClient.axios.post<Token>("/auth/login", data);
    return response.data;
  },

  me: async (): Promise<UserResponse> => {
    const response = await apiClient.axios.get<UserResponse>("/auth/me");
    return response.data;
  },
};
