import { apiClient } from "./client";
import type { APIKeyCreate, APIKeyResponse, APIKeyInfo } from "@/types/api";

export const apiKeysApi = {
  list: async (): Promise<APIKeyInfo[]> => {
    const response = await apiClient.axios.get<APIKeyInfo[]>("/api-keys/");
    return response.data;
  },

  create: async (data: APIKeyCreate): Promise<APIKeyResponse> => {
    const response = await apiClient.axios.post<APIKeyResponse>("/api-keys/", data);
    return response.data;
  },

  revoke: async (keyId: string): Promise<void> => {
    await apiClient.axios.delete(`/api-keys/${keyId}`);
  },
};
