"use client";

import { use, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useQuery } from "@tanstack/react-query";
import {
  ArrowLeft,
  CheckCircle2,
  XCircle,
  Clock,
  Loader2,
  Download,
  FileText
} from "lucide-react";
import { jobsApi } from "@/lib/api";
import { useAuthStore } from "@/lib/store/auth";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { formatDistanceToNow } from "date-fns";

interface PageProps {
  params: Promise<{ id: string }>;
}

export default function JobStatusPage({ params }: PageProps) {
  const resolvedParams = use(params);
  const router = useRouter();
  const isAuthenticated = useAuthStore((state) => state.token !== null && state.user !== null);
  const [showResult, setShowResult] = useState(false);

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login");
    }
  }, [isAuthenticated, router]);

  // Poll for job status
  const { data: status, isLoading } = useQuery({
    queryKey: ["job-status", resolvedParams.id],
    queryFn: () => jobsApi.getStatus(resolvedParams.id),
    refetchInterval: (query) => {
      const status = query.state.data?.status;
      // Stop polling if job is completed or failed
      return status === "completed" || status === "failed" ? false : 3000;
    },
  });

  // Fetch result when job is completed
  const { data: result } = useQuery({
    queryKey: ["job-result", resolvedParams.id],
    queryFn: () => jobsApi.getResult(resolvedParams.id),
    enabled: status?.status === "completed" && showResult,
  });

  // Fetch pages for PDF documents
  const { data: pages } = useQuery({
    queryKey: ["job-pages", resolvedParams.id],
    queryFn: () => jobsApi.getPages(resolvedParams.id),
    enabled: status?.type === "main" && (status?.total_pages ?? 0) > 0,
    refetchInterval: (query) => {
      const data = query.state.data;
      return data && data.pages_completed + data.pages_failed < data.total_pages
        ? 3000
        : false;
    },
  });

  const getStatusIcon = () => {
    switch (status?.status) {
      case "completed":
        return <CheckCircle2 className="h-12 w-12 text-green-500" />;
      case "failed":
        return <XCircle className="h-12 w-12 text-red-500" />;
      case "processing":
        return <Loader2 className="h-12 w-12 text-blue-500 animate-spin" />;
      default:
        return <Clock className="h-12 w-12 text-yellow-500" />;
    }
  };

  const getStatusColor = () => {
    switch (status?.status) {
      case "completed":
        return "text-green-500";
      case "failed":
        return "text-red-500";
      case "processing":
        return "text-blue-500";
      default:
        return "text-yellow-500";
    }
  };

  const downloadMarkdown = () => {
    if (!result) return;
    const blob = new Blob([result.result.markdown], { type: "text/markdown" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${status?.name || resolvedParams.id}.md`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  if (isLoading) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-muted">
      {/* Header */}
      <header className="border-b bg-background/95 backdrop-blur">
        <div className="container mx-auto px-4 py-4">
          <Button
            variant="ghost"
            onClick={() => router.push("/dashboard")}
            className="mb-2"
          >
            <ArrowLeft className="h-4 w-4 mr-2" />
            Back to Dashboard
          </Button>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-12">
        <div className="max-w-4xl mx-auto space-y-6">
          {/* Status Card */}
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Job Status</CardTitle>
                  <CardDescription className="mt-1">
                    {status?.name || `Job ID: ${resolvedParams.id}`}
                  </CardDescription>
                </div>
                <div>{getStatusIcon()}</div>
              </div>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Status Info */}
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <div>
                  <p className="text-sm text-muted-foreground">Status</p>
                  <p className={`font-medium capitalize ${getStatusColor()}`}>
                    {status?.status}
                  </p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Progress</p>
                  <p className="font-medium">{status?.progress}%</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Created</p>
                  <p className="font-medium text-sm">
                    {status?.created_at
                      ? formatDistanceToNow(new Date(status.created_at), {
                          addSuffix: true,
                        })
                      : "-"}
                  </p>
                </div>
                {status?.completed_at && (
                  <div>
                    <p className="text-sm text-muted-foreground">Completed</p>
                    <p className="font-medium text-sm">
                      {formatDistanceToNow(new Date(status.completed_at), {
                        addSuffix: true,
                      })}
                    </p>
                  </div>
                )}
              </div>

              {/* Progress Bar */}
              <div className="space-y-2">
                <div className="w-full bg-secondary rounded-full h-2">
                  <div
                    className="bg-primary h-2 rounded-full transition-all duration-300"
                    style={{ width: `${status?.progress || 0}%` }}
                  />
                </div>
              </div>

              {/* Error Message */}
              {status?.error && (
                <div className="rounded-md bg-destructive/10 border border-destructive/20 p-4 text-sm text-destructive">
                  <p className="font-medium">Error:</p>
                  <p className="mt-1">{status.error}</p>
                </div>
              )}

              {/* Pages Progress */}
              {pages && pages.total_pages > 0 && (
                <div className="space-y-2">
                  <p className="text-sm font-medium">
                    Pages: {pages.pages_completed} / {pages.total_pages} completed
                  </p>
                  {pages.pages_failed > 0 && (
                    <p className="text-sm text-destructive">
                      {pages.pages_failed} pages failed
                    </p>
                  )}
                </div>
              )}

              {/* Actions */}
              {status?.status === "completed" && (
                <div className="flex gap-4">
                  <Button onClick={() => setShowResult(!showResult)} className="flex-1">
                    <FileText className="h-4 w-4 mr-2" />
                    {showResult ? "Hide" : "View"} Result
                  </Button>
                  <Button onClick={downloadMarkdown} variant="outline">
                    <Download className="h-4 w-4 mr-2" />
                    Download
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>

          {/* Result Card */}
          {showResult && result && (
            <Card>
              <CardHeader>
                <CardTitle>Markdown Output</CardTitle>
                <CardDescription>
                  {result.result.metadata.pages && `${result.result.metadata.pages} pages • `}
                  {result.result.metadata.format.toUpperCase()}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="bg-muted rounded-lg p-4 max-h-[600px] overflow-y-auto">
                  <pre className="text-sm whitespace-pre-wrap font-mono">
                    {result.result.markdown}
                  </pre>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </main>
    </div>
  );
}
