import { apiClient } from "./client";
import type {
  JobCreatedResponse,
  JobStatusResponse,
  JobResultResponse,
  JobPagesResponse,
  JobsListParams,
  SearchParams,
  ConvertRequest,
  UploadRequest,
} from "@/types/api";

export const jobsApi = {
  upload: async (data: UploadRequest): Promise<JobCreatedResponse> => {
    const formData = new FormData();
    formData.append("file", data.file);
    if (data.name) {
      formData.append("name", data.name);
    }

    const response = await apiClient.axios.post<JobCreatedResponse>("/upload", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return response.data;
  },

  convert: async (data: ConvertRequest, authHeader?: string): Promise<JobCreatedResponse> => {
    const formData = new FormData();
    formData.append("source_type", data.source_type);

    if (data.source) {
      formData.append("source", data.source);
    }

    if (data.file) {
      formData.append("file", data.file);
    }

    if (data.name) {
      formData.append("name", data.name);
    }

    const headers: Record<string, string> = {
      "Content-Type": "multipart/form-data",
    };

    if (authHeader) {
      headers.Authorization = authHeader;
    }

    const response = await apiClient.axios.post<JobCreatedResponse>("/convert", formData, {
      headers,
    });
    return response.data;
  },

  getStatus: async (jobId: string): Promise<JobStatusResponse> => {
    const response = await apiClient.axios.get<JobStatusResponse>(`/jobs/${jobId}`);
    return response.data;
  },

  getResult: async (jobId: string): Promise<JobResultResponse> => {
    const response = await apiClient.axios.get<JobResultResponse>(`/jobs/${jobId}/result`);
    return response.data;
  },

  getPages: async (jobId: string): Promise<JobPagesResponse> => {
    const response = await apiClient.axios.get<JobPagesResponse>(`/jobs/${jobId}/pages`);
    return response.data;
  },

  getPageStatus: async (jobId: string, pageNumber: number): Promise<JobStatusResponse> => {
    const response = await apiClient.axios.get<JobStatusResponse>(
      `/jobs/${jobId}/pages/${pageNumber}/status`
    );
    return response.data;
  },

  getPageResult: async (jobId: string, pageNumber: number): Promise<JobResultResponse> => {
    const response = await apiClient.axios.get<JobResultResponse>(
      `/jobs/${jobId}/pages/${pageNumber}/result`
    );
    return response.data;
  },

  list: async (params?: JobsListParams): Promise<JobStatusResponse[]> => {
    const response = await apiClient.axios.get<{ jobs: JobStatusResponse[] }>("/jobs", { params });
    return response.data.jobs;
  },

  search: async (params: SearchParams): Promise<JobStatusResponse[]> => {
    const response = await apiClient.axios.get<JobStatusResponse[]>("/search", { params });
    return response.data;
  },

  delete: async (jobId: string): Promise<string> => {
    const response = await apiClient.axios.delete<string>(`/jobs/${jobId}`);
    return response.data;
  },
};
